<template>
  <div class="container flex-fill d-flex flex-column my-3">
    <!-- spinner -->
    <div v-if="loading" class="spinner-border m-auto text-primary" role="status">
      <span class="sr-only">Carregando...</span>
    </div>
    <!-- palavra do presidente -->
    <div v-else-if="!loading && texto" class="card">
      <div class="card-body">
        <!-- titulo -->
        <h5 class="text-center">PALAVRA DO PRESIDENTE</h5>
        <!-- texto -->
        <p class="text-justify" style="white-space: pre-wrap">{{texto}}</p>
        <!-- autor -->
        <div class="text-center">
          <p>O futuro chegou. Venha conosco!</p>
          <img v-if="foto" :src="foto" class="img-fluid presidente" alt="">
          <h5 v-if="nome" class="mb-0">{{nome}}</h5>
          <h6 class="text-primary">Presidente do COFECI</h6>
        </div>
      </div>
    </div>
    <!-- falha -->
    <div v-else class="text-center flex-fill d-flex flex-column align-items-center justify-content-center">
      <i class="fas fa-exclamation-triangle fa-3x text-warning"></i>
      <p class="mt-2">Não há comunicado do presidente</p>
      <router-link class="btn btn-sm btn-secondary" :to="{ name: 'menu' }">voltar</router-link>
    </div>
  </div>
</template>

<script>
  import ImageHelper from "../helpers/ImageHelper";
  export default {
    name: "PalavraCofeci",
    data: () => ({
      texto: '',
      nome: '',
      foto: null,
      loading: true
    }),
    async created() {
      this.obterLocal();
      try {
        await this.obterDaWeb();
      } finally {
        this.loading = false;
      }
    },
    methods: {
      obterLocal() {
        let dados = localStorage.getItem('presidente_cofeci');
        if (!dados) return false;
        dados = JSON.parse(dados);
        this.texto = dados.texto;
        this.nome = dados.nome;
        this.foto = dados.foto;
        return true;
      },
      async obterDaWeb() {
        //Pega os dados da WEB
        const formData = new FormData();
        formData.append('idregional', '0');
        const { data: presidente } = await this.$http.axios.post(this.$http.appApi.defaults.baseURL + '/presidente/obter_presidente.php', formData);
        this.texto = presidente.texto;
        this.nome = presidente.nome;
        const { data: foto } = await this.$http.axios.get(this.$http.appApi.defaults.baseURL + '/presidente/obter_presidente_foto.php?id=0', { responseType: 'blob' });
        this.foto = await ImageHelper.blobToDataURI_async(foto);
        localStorage.setItem('presidente_cofeci', JSON.stringify({ texto: this.texto, nome: this.nome, foto: this.foto }));
      }
    }
  }
</script>

<style scoped>
  .presidente {
    border-radius: 50%;
    width: 8rem;
    height: 8rem;
    object-fit: cover;
    box-shadow: 0 2px 5px 0 rgba(0,0,0,.16), 0 2px 10px 0 rgba(0,0,0,.12);
  }
</style>
